import React from "react"
import { Link } from "gatsby"

const BlogItem = ({props}) => (

        <Link className='my-link' to={props.path}>
            <p></p>
            <div>
                <p>
                <div className='my-h2'>{props.title}</div>
                <div className='my-body'>{props.desc}</div>
                </p>
            </div>
            <p></p>
        </Link>

);

export default BlogItem